import { Component, OnInit, Input } from '@angular/core';
import { SsLoaderService } from './ss-loader.service';

@Component({
  selector: 'ss-loader',
  template: `
  <div id="loading" class="loader" [ngStyle]="{'background': 'url(' + d_img + ') no-repeat center center','background-size':d_gif_size+'px', 'background-color':d_color}" *ngIf="showLoader"></div>
  `,
  styles: [`
  #loading {
    position: fixed;
    width: 100%;
    height: 100vh;
    margin:0px !important;
    // background:url('data:image/svg+xml;base64,') no-repeat center center;
    // background-color: #0007;
    // background-size: 10%;
    background-position:center;
    z-index: 9999;
    cursor: progress;
  }
  body {
    margin:0px !important;
    }`
  ]
})
export class SsLoaderComponent implements OnInit {

  @Input() imageUrl: string;
  @Input() size: number;
  @Input() bgColor: any;
  showLoader: boolean;

  d_img = `data:image/svg+xml;base64,`;
  d_color = '#0007';
  d_gif_size = 100;

  constructor(private loaderService: SsLoaderService) { }
  visible: boolean;
  ngOnInit() {
    this.loaderService.status.subscribe((val: boolean) => {
      if(this.imageUrl != undefined){
        this.d_img = this.imageUrl;
      }
      if(this.size != undefined){
        this.d_gif_size = this.size;
      }
      if(this.bgColor != undefined){
        this.d_color = this.bgColor;
      }
      this.showLoader = val;
    });
  }

}
